/*

text
@extend .text-main--low

*/
html,
body {
  scroll-behavior: smooth;
}

.header {
  position: absolute;
  width: 100%;
  @extend .text-main--low;
  z-index: 50;
}
.logo {
  cursor: pointer;
  animation-name: spin;
  animation-duration: 1000ms;
  transition: opacity 0.5s, top 0.9s;
  position: relative;
  top: 0;
  opacity: 1;
  @keyframes spin {
    0% {
      transform: translateY(-250%);
    }
    100% {
      transform: translateY(0%);
    }
  }
  &:hover {
    top: -5px;
    opacity: 0.5;
  }
}

.logo__img {
  width: 200px;
  height: 50px;
}

.header-container {
  width: inherit;
  margin-top: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
    margin-top: 52px;
  }
}
.menu {
  display: flex;
  align-items: center;
}
//BUTTONS
.buttons {
  display: flex;
  align-items: center;
  @media screen and (min-width: 480px) and (max-width: 1199px) {
    flex-grow: 1;
    justify-content: flex-end;
  }
}

.buttons .burger-menu-btn[aria-expanded="true"] {
  display: none;
}

.burger-menu-btn{
  
  background: none;
  border: 0;
  cursor: pointer;
  @media screen and (min-width: 1200px) {
    display: none;
  }
}
button.burger-menu-btn[aria-expanded="true"] {
  display: none;
  & + button {
    display: none;
  }
}
.overlay-block {
  pointer-events: none;
}



.buy-now__btn {
  cursor: pointer;
  background-color: $color-card;
  border-radius: 22px;
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: baseline;
  border: 0px;

  padding: 11px 0px;
  font-family: $font-main;
  font-weight: 700;
  color: $color-boss;
  transition-duration: 1000ms;
  &:hover,
  &:focus {
    background-color: $color-hover-light;
  }
  @media screen and (max-width: 480px) {
    display: none;
  }
  @media screen and (min-width: 768px) {
    margin-left: 32px;
  }
}
.btn-txt {
  margin-right: 12px;
}
//BUTTONS-END__________________END__________________________//
//List_________________________LIST_________________________//
.nav__list {
  display: none;
  @media screen and (min-width: 1200px) {
    display: flex;
    margin: auto;
  }
}
.nav__link {
  padding: 10px 0px 10px 0px;
  text-decoration: none;
  font-family: $font-main;
  transition-duration: 1000ms;
  font-weight: 700;
  font-size: 16px;
  @extend .text-main--low;
  color: $color-card;
  &:hover,
  &:focus {
    color: $color-boss;
  }
}
.nav__item {
  &:not(:first-child) {
    margin-left: 35px;
  }
}
.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (min-width: 1200px) {
    flex-grow: 1;
  }
}

