.gallery {
  @media screen and (min-width: 1199px) {
    padding-bottom: 80px;
  }
  @media screen and (min-width: 1200px) {
    padding-bottom: 120px;
  }
  background: linear-gradient(to bottom, #fff5f6 30%, #ffffff 0);
}
.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

img {
  display: block;
  max-width: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.page-header__container {
  display: flex;
}

.page-nav__list {
  display: flex;
}

.slick-track {
  display: flex;
}

.gallery__item {
  width: 370px;
  margin: 0 auto;
  text-align: center;
}

.masonry__thumb img {
  width: 100%;
  height: 100%;
}

.gread__list {
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 4px;
}
.gread__list {
  grid-gap: 8px;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(9, 1fr);
}

.gread__item {
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.gread__item:hover {
  transform: rotate(1turn) scale(1.1);
}

.gread__item-1 {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
}
.gread__item-1 {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 4;
  grid-row-end: 7;
}

