.masonry__item:first-child:hover,
.masonry__img:hover {
  z-index: 1;
}

.masonry__item:first-child,
.masonry__item:last-child,
.masonry__img {
  transition: transform 500ms;
}

.masonry__item:first-child:hover,
.masonry__item:last-child:hover,
.masonry__img:hover {
  transform: scale(1.2);
}
